const state = {
  currentField: 0,
  isShowField: false,
  fields: [
    '電機系 研究所',
    '資訊系 研究所',
    '電機系 大學部',
    '資訊系 大學部',
  ],
  color: [
    '#ffaaa7',
    '#fdffbc',
    '#ffeebb',
    '#d4e2d4'
  ]
}

const getters = {
  allFields: state => {
    return state.fields;
  },
  fieldById: state => {
    return (fieldId) => {
      return state.fields[fieldId];
    }
  },
  currentField: state=> {
    return state.currentField;
  },
  colorByFieldId: state => {
    return (fieldId) => {
      return state.color[fieldId];
    }
  }
}

const mutations = {
  setCurrentField: (state, fieldId) => {
    state.currentField = fieldId;
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations
}